.sign {
  /* position: absolute; */
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
  /* flex-wrap: wrap; */
  width: 100%;
  /* height: 50%; */
  /* background-image: radial-gradient(
        ellipse 50% 35% at 50% 50%,
        #6b1839,
        transparent
      ); */
  z-index: 0;
  /* transform: translate(-50%, -50%); */
  letter-spacing: 2;
  /* left: 35%;
  top: 65%; */
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 2em;
  color: #06cd32;
  text-shadow: 0 0 0.6rem #06cd32, 0 0 1.5rem #2cda52,
    -0.2rem 0.1rem 1rem #385cf4, 0.2rem 0.1rem 1rem #385cf4,
    0 -0.5rem 2rem #0031ff, 0 0.5rem 3rem #0031ff;
  animation: shine 2s forwards, flicker 3s infinite;
}

.sign p {
  margin: 0.8em 0;
}

@keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #06cd32;
    text-shadow: 0 0 0.6rem #06cd32, 0 0 1.5rem #385cf4,
      -0.2rem 0.1rem 1rem #385cf4, 0.2rem 0.1rem 1rem #385cf4,
      0 -0.5rem 2rem #0031ff, 0 0.5rem 3rem #0031ff;
  }
  28%,
  33% {
    color: #385cf4;
    text-shadow: none;
  }
  82%,
  97% {
    color: #0031ff;
    text-shadow: none;
  }
}

.flicker {
  animation: shine 2s forwards, blink 3s 2s infinite;
}

.fastFlicker {
  animation: shine 2s forwards, blink 10s 1s infinite;
}

@keyframes shine {
  0% {
    color: #06cd32;
    text-shadow: none;
  }
  100% {
    color: #06cd32;
    text-shadow: 0 0 0.6rem #06cd32, 0 0 1.5rem #385cf4,
      -0.2rem 0.1rem 1rem #385cf4, 0.2rem 0.1rem 1rem #385cf4,
      0 -0.5rem 2rem #0031ff, 0 0.5rem 3rem #0031ff;
  }
}

