.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.wrap {
  width: 1000px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left {
  width: 40%;
  height: 480px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.right {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}

.right img {
  width: 80%;
}

.right video {
  width: 80%;
}

button {
  margin-top: 48px;
  font-size: 14px;
  padding: 12px 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #fff;
  background-color: #3358fd;
  border: 1px solid #3358fd;
  text-transform: uppercase;
}

button:hover {
  transition: all 0.2s;
  color: #3358fd;
  background-color: transparent;
  border: 1px solid #3358fd;
}

button:focus {
  outline: none;
}
